html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border: none;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #FAFBFF;
  color: #000;
}
@media screen and (max-width: 1280px) {
  body.lock {
    overflow: hidden;
  }
}

.dashboard {
  display: flex;
}
@media screen and (max-width: 1280px) {
  .dashboard {
    display: block;
  }
}

.burger {
  display: none;
}
@media screen and (max-width: 1280px) {
  .burger {
    position: relative;
    top: 65px;
    left: 21px;
    z-index: 2;
    width: 30px;
    height: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
  }
  .burger::before {
    content: "";
    display: block;
    width: 30px;
    height: 3px;
    background: #9197B3;
    transition: all 0.2s ease;
  }
  .burger::after {
    content: "";
    display: block;
    width: 15px;
    height: 3px;
    background: #9197B3;
    transition: all 0.2s ease;
  }
  .burger__inner {
    display: block;
    width: 22px;
    height: 3px;
    background: #9197B3;
  }
  .burger__inner.open {
    display: none;
  }
  .burger.open {
    position: absolute;
    top: 48px;
    left: 260px;
  }
  .burger.open::before {
    transform: rotate(45deg);
    position: relative;
    top: 12px;
    width: 20px;
  }
  .burger.open::after {
    transform: rotate(-45deg);
    width: 20px;
    position: relative;
    bottom: 15px;
  }
}
@media screen and (max-width: 768px) {
  .burger {
    top: 46px;
    left: 10px;
  }
}

.sidebar {
  display: flex;
  flex-direction: column;
  padding: 36px 28px 76px;
  width: 306px;
  background-color: #FFF;
}
.sidebar__header {
  display: flex;
  align-items: center;
  margin-bottom: 54px;
}
.sidebar__logo {
  width: 37px;
  height: 37px;
  margin-right: 8px;
}
.sidebar__heading {
  color: #000;
  font-size: 26px;
  font-weight: 600;
  letter-spacing: 0.26px;
}
.sidebar__version {
  display: inline-block;
  margin-left: 4px;
  color: #838383;
  font-size: 10px;
  font-weight: 500;
  letter-spacing: -0.1px;
}
.sidebar__item {
  width: 250px;
  border-radius: 8px;
  font-size: 14px;
  letter-spacing: -0.14px;
  padding: 11px 8px 11px 11px;
  color: #9197B3;
}
.sidebar__item:not(:last-child) {
  margin-bottom: 18px;
}
.sidebar__item_active {
  width: 250px;
  height: 46px;
  border-radius: 8px;
  padding: 11px 8px 11px 11px;
  font-size: 14px;
  letter-spacing: -0.14px;
  color: #FFF;
  background: #5932EA;
  margin-bottom: 17px;
}
.sidebar__link {
  display: flex;
  align-items: center;
}
.sidebar__icon {
  width: 24px;
  height: 24px;
  margin-right: 14px;
}
.sidebar__chevron {
  width: 16px;
  height: 16px;
  margin-left: auto;
  stroke: #9197B3;
}
.sidebar__chevron_active {
  stroke: #FFF;
}
.sidebar__chevron_transp {
  stroke: none;
}
.sidebar__user {
  display: flex;
  margin-top: auto;
}
.sidebar__user-logo {
  width: 42px;
  height: 42px;
  flex-shrink: 0;
  border-radius: 42px;
  background: url("../assets/images/user-logo.jpg"), lightgray 50%/cover no-repeat;
  margin-right: 12px;
}
.sidebar__user-name {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.14px;
}
.sidebar__user-position {
  color: #757575;
  font-size: 12px;
  letter-spacing: 0.12px;
}
@media screen and (max-width: 1280px) {
  .sidebar {
    display: none;
  }
  .sidebar.open {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100dvh;
    padding-left: 12px;
    transition: all 0.3s ease;
    overflow: auto;
  }
  .sidebar__header {
    margin-bottom: 34px;
  }
  .sidebar__item:not(:last-child) {
    margin-bottom: 0;
  }
  .sidebar__item_active {
    margin-bottom: 0;
  }
}

.customers {
  width: 100%;
  padding: 41px 71px;
}
.customers__heading {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 51px;
}
.customers__list-wrap {
  background: #FFF;
  border-radius: 32px;
  padding: 30px 40px 40px 38px;
}
.customers__list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.customers__list-heading {
  font-size: 22px;
  font-weight: 600;
  letter-spacing: -0.22px;
  margin-bottom: 7px;
}
.customers__list-subheading {
  color: #16C098;
  font-size: 14px;
  letter-spacing: -0.14px;
  margin-bottom: 16px;
}
.customers__search-wrap {
  position: relative;
}
.customers__search {
  width: 216px;
  height: 38px;
  flex-shrink: 0;
  border-radius: 10px;
  background: #F9FBFF;
  padding: 10px 10px 10px 40px;
  margin-right: 13px;
}
.customers__search::placeholder {
  color: #B5B7C0;
  font-size: 12px;
  letter-spacing: -0.12px;
}
.customers__search:focus {
  outline: none;
}
.customers__search-icon {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 7px;
  left: 8px;
}
.customers__list {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 30px;
}
.customers__table-row {
  border-bottom: 1px solid #EEE;
}
.customers__table-heading {
  color: #B5B7C0;
  padding: 24px 0 14px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.14px;
  text-align: left;
}
.customers__table-heading:last-of-type {
  text-align: center;
}
.customers__table-data {
  color: #292D32;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.14px;
  padding: 18px 0;
  text-align: left;
}
.customers__table-data:last-of-type {
  text-align: end;
}
.customers__status_active, .customers__status_inactive {
  padding: 4px 12px;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
}
.customers__status_active {
  display: inline-flex;
  width: 80px;
  color: #008767;
  border: 1px solid #00B087;
  background: rgba(22, 192, 152, 0.38);
}
.customers__status_inactive {
  display: inline-flex;
  color: #DF0404;
  border: 1px solid #DF0404;
  background: #FFC5C5;
}
.customers__card-wrap {
  display: none;
}
.customers__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.customers__pagination-info {
  color: #B5B7C0;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.14px;
}
.customers__pagination-item, .customers__pagination-item_active {
  width: 26px;
  height: 24px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  border: 1px solid #EEE;
  background: #F5F5F5;
  font-size: 12px;
  font-weight: 500;
  line-height: 100%;
}
.customers__pagination-item:not(:last-child), .customers__pagination-item_active:not(:last-child) {
  margin-right: 12px;
}
.customers__pagination-item:nth-child(6), .customers__pagination-item_active:nth-child(6) {
  width: 0;
  height: 0;
  background: none;
  border: none;
}
.customers__pagination-item_active {
  color: #FFF;
  border: 1px solid #5932EA;
  background: #5932EA;
}
.customers__pagination-link {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 1280px) {
  .customers__heading {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 21px;
  }
  .customers__list-wrap {
    padding: 15px 20px 20px 19px;
  }
  .customers__list-heading {
    font-size: 18px;
    margin-bottom: 3px;
  }
  .customers__list-subheading {
    font-size: 12px;
    margin-bottom: 0;
  }
  .customers__search {
    width: 216px;
    height: 34px;
    padding: 8px 8px 8px 38px;
    margin-right: 0;
  }
  .customers__search-icon {
    width: 20px;
    height: 20px;
  }
  .customers__table-heading {
    padding: 18px 12px 12px 0;
    font-size: 12px;
  }
  .customers__table-data {
    font-size: 12px;
    padding: 10px 12px 10px 0;
  }
  .customers__status_active, .customers__status_inactive {
    padding: 2px 6px;
  }
  .customers__status_active {
    width: 62px;
  }
  .customers__pagination-info {
    font-size: 12px;
  }
  .customers__pagination-item, .customers__pagination-item_active {
    width: 22px;
    height: 20px;
    font-size: 10px;
  }
  .customers__pagination-item:not(:last-child), .customers__pagination-item_active:not(:last-child) {
    margin-right: 8px;
  }
}
@media screen and (max-width: 768px) {
  .customers {
    padding: 21px 21px 21px 41px;
  }
  .customers__list {
    display: none;
  }
  .customers__heading {
    text-align: center;
  }
  .customers__list-header {
    flex-direction: column;
    margin-bottom: 25px;
  }
  .customers__list-heading-wrap {
    text-align: center;
    margin-bottom: 20px;
  }
  .customers__card-wrap {
    display: block;
    border: 1px solid #EEE;
    margin-bottom: 10px;
    border-radius: 2%;
  }
  .customers__card {
    padding: 12px;
    display: flex;
    justify-content: space-between;
  }
  .customers__card-heading {
    color: #B5B7C0;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: -0.14px;
  }
  .customers__card-data {
    color: #292D32;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: -0.14px;
    text-align: end;
  }
  .customers__footer {
    flex-direction: column;
  }
  .customers__pagination-info {
    margin-bottom: 15px;
    text-align: center;
  }
  .customers__pagination-list {
    text-align: center;
  }
}